/* =B-VLIST
---------------------------------------------------------
*/

.b-vlist {
    padding:0;
    list-style:none;
    margin-left: 1.3em;
}
.b-vlist li {

    padding-bottom: 5px;
}

.b-vlist LI:before
{ padding:0;
    margin:0;
    list-style:none;
    position: absolute;
    margin-left: -1.3em;
    content: "—";
}

/*ie*/

.b-vlist LI, {
    zoom: expression(runtimeStyle.zoom = 1,insertBefore(createElement("m"),firstChild));
}

.b-vlist LI m {
    position: absolute;
    margin-left: -1.3em;
    text-decoration: none;
    zoom: expression(runtimeStyle.zoom = 1,innerText = "—");
    margin-top:-0.2em;
}


/* =END B_VLIST
---------------------------------------------------------
*/
